/*---------------------- 01. STYLES INIT----------------------*/
@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800&display=swap&subset=cyrillic");
html.js {
  margin-top: 0 !important;
  font-size: 14px;
}

body {
  min-height: 100vh;
  overflow-x: hidden;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #222222;
  position: relative;
  background: #ffffff;
}

strong, b {
  font-weight: 800;
}

.circle .ugm-map-wrapper {
  position: relative;
  overflow: hidden;
  border-radius: 50%;
}

.main-wrapper {
  min-height: 100vh;
}

::-moz-selection {
  background: #000;
  color: #ffffff;
}

::selection {
  background: #000;
  color: #ffffff;
}

::-moz-selection {
  background: #000;
  color: #ffffff;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  -webkit-font-smoothing: antialiased;
}

:focus {
  outline: 0;
}

a,
a:hover,
a:focus {
  color: #222222;
  outline: 0;
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease;
  text-decoration: none;
}

a {
  text-decoration: underline;
}

.text-light a {
  color: #ffffff;
}

.no-scroll {
  overflow-y: hidden !important;
}

.overflow-hidden {
  overflow: hidden;
}

